@import url(https://fonts.googleapis.com/css2?family=Chathura:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chathura:wght@700;800&family=Roboto&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display:flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
.App{
  width: 372px;
  height: 712px;
  margin:100px auto;

  display:flex;
  flex-direction:column;
}
.number-field{
    width:100%;
    height:70px;
    margin-bottom: 30px;
    direction: rtl;
   
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button
{
-webkit-appearance: none;
margin:0;
}
input[type=number]
{
    width:100%;
    height:100%;
    border:none;
    margin-top: 50px;
    font-size: 150px;
    font-weight:400;
    padding: 0px 24px 0px 0px;
    font-family: 'Chathura', sans-serif;

}
input[type=number]:focus
{
    border:none;
    outline:none;
}
.controls{
    width:100%;
    margin-top:60px;
    height:60%;
    display:flex;
    flex-direction:column;
    text-align: center;;

}
.row-one,.row-two,.row-three,.row-four,.row-five{
    display:flex;
    justify-content:space-around;
    align-items: center;
    text-align:center;
    font-size:32px;
    margin-top:15px;
}
.plus,.minus,.multiply,.division,.equal,.plus-minus,.percent,.AC,
.one,.two,.three,.four,.five,.six,.seven,.eight,.nine,.equal,.dot,.zero
{
    
    width: 60px;
    border-radius: 50%;
    padding: 10px;
    font-size:32px;
    font-family: 'Roboto', sans-serif;
}
.plus:hover,.minus:hover,.multiply:hover,.division:hover,.equal:hover,.plus-minus:hover,.percent:hover,.AC:hover,
.one:hover,.two:hover,.three:hover,.four:hover,.five:hover,.six:hover,.seven:hover,.eight:hover,.nine:hover,.equal:hover,.dot:hover,.zero-container:hover{
cursor:pointer;
}
.plus,.minus,.multiply,.division,.equal{
    color:white;
    background-color:#34509E;
}
.AC,.plus-minus,.percent{
    background-color: #8490FF;
}
.one,.two,.three,.four,.five,.six,.seven,.eight,.nine,.equal,.dot,.zero
{
    background-color:#212638;
    color:white;
}
.zero-container{
   width:150px;
   display:flex;
   justify-content:space-between;
   background-color:#212638;
    color:white;
    border-radius: 32px;
}

